/* src/app/app.scss */
.correct {
  background-color: #52c41a;
  border-color: #52c41a;
  color: white;
}
.incorrect {
  background-color: #ff4d4f;
  border-color: #ff4d4f;
  color: white;
}
.hint {
  background-color: #faad14;
  border-color: #faad14;
  color: white;
}
.container {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 20px;
  box-sizing: border-box;
}
.content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 0;
}
.words-container {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.grid-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 4px;
  justify-content: center;
}
.letter {
  text-align: center;
  text-transform: uppercase;
  font-size: clamp(12px, 5vw, 18px);
  font-weight: bold;
  width: 50px;
  height: 50px;
  border: 1px solid #434343;
  border-radius: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transform: scale(1);
}
@media (max-width: 400px) {
  .letter {
    width: 35px;
    height: 35px;
  }
}
.retry-button-container {
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 16px 0;
}
.retry-button {
  margin: 10px;
}
.keyboard-container {
  flex-shrink: 0;
  width: 100%;
}
/*# sourceMappingURL=app.css.map */
